"use client";

import { useState, useRef, useTransition } from "react";
import Image from "next/image";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import type { Template } from "@/lib/definitions";
import { Eye, Trash2, Star, Loader2, Copy, Download } from "lucide-react";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { deleteTemplate, toggleTemplateProStatus } from "@/lib/actions";
import { useToast } from "@/hooks/use-toast";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";

interface TemplateCardProps {
  template: Template;
  onPreview: (template: Template) => void;
  onViewVariations: (template: Template) => void;
}

export function TemplateCard({ template, onPreview, onViewVariations }: TemplateCardProps) {
  const [isHovered, setIsHovered] = useState(false);
  const videoRef = useRef<HTMLVideoElement>(null);
  const [isPending, startTransition] = useTransition();
  const { toast } = useToast();

  const hasVariations = template.thumbUrl_v2 || template.thumbUrl_v3;

  const handleMouseEnter = () => {
    setIsHovered(true);
    videoRef.current?.play().catch(() => {});
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    videoRef.current?.pause();
  };

  const handleDelete = () => {
    startTransition(async () => {
      const result = await deleteTemplate(template.id || template.templateId);
      if (result?.error) {
        toast({
          variant: "destructive",
          title: "Error",
          description: result.error,
        });
      } else {
        toast({
          title: "Success",
          description: result.message,
        });
      }
    });
  };

  const handleTogglePro = () => {
    startTransition(async () => {
      const result = await toggleTemplateProStatus(template.id || template.templateId, template.pro);
       if (result?.error) {
        toast({
          variant: "destructive",
          title: "Error",
          description: result.error,
        });
      } else {
        toast({
          title: "Success",
          description: result.message,
        });
      }
    });
  };

  const handleDownload = () => {
    const jsonString = JSON.stringify(template, null, 2);
    const blob = new Blob([jsonString], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `${template.name || 'template'}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const aspectRatio = template.Width && template.Height ? `${template.Width} / ${template.Height}` : '9 / 16';

  return (
    <Card
      className="overflow-hidden break-inside-avoid shadow-md transition-shadow duration-300 hover:shadow-xl"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <CardContent 
        className="p-0 relative"
        style={{ aspectRatio }}
      >
        {isPending && (
          <div className="absolute inset-0 z-10 bg-background/50 flex items-center justify-center">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
          </div>
        )}
        <div className="absolute inset-0">
          {isHovered && template.vidUrl ? (
            <video
              ref={videoRef}
              src={template.vidUrl}
              className="h-full w-full object-cover"
              loop
              muted
              playsInline
            />
          ) : (
            <Image
              src={template.thumbUrl}
              alt={template.name}
              fill
              className="object-cover"
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            />
          )}
        </div>
        <div className="absolute top-2 right-2 flex gap-2">
            {template.pro && <Badge variant="destructive" className="flex items-center gap-1"><Star className="h-3 w-3"/> PRO</Badge>}
            {hasVariations && <Badge variant="default" className="flex items-center gap-1"><Copy className="h-3 w-3"/> Variations</Badge>}
        </div>
      </CardContent>
      <CardFooter className="flex flex-col items-start gap-3 p-4">
        <h3 className="font-semibold truncate w-full text-sm">{template.name}</h3>
        <div className="flex flex-wrap gap-1">
          {Array.isArray(template.category) && template.category.map((cat) => (
            <Badge key={cat} variant="secondary" className="text-xs">{cat}</Badge>
          ))}
        </div>
        
        <div className="w-full grid grid-cols-2 gap-2 mt-2">
          <Button
            className="w-full"
            onClick={() => onPreview(template)}
            variant="outline"
            size="sm"
          >
            <Eye className="mr-2 h-4 w-4" />
            Preview
          </Button>

          <Button
            variant="outline"
            size="sm"
            className="w-full"
            onClick={handleDownload}
          >
            <Download className="mr-2 h-4 w-4" /> JSON
          </Button>
        </div>
        
        <div className="w-full">
            <AlertDialog>
              <AlertDialogTrigger asChild>
                <Button variant="destructive" size="sm" className="w-full">
                  <Trash2 className="mr-2 h-4 w-4" /> Delete
                </Button>
              </AlertDialogTrigger>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
                  <AlertDialogDescription>
                    This action cannot be undone. This will permanently delete the
                    template file and remove its data from the server.
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel>Cancel</AlertDialogCancel>
                  <AlertDialogAction onClick={handleDelete}>
                    Continue
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>
        </div>


        {hasVariations && (
            <Button
                className="w-full"
                onClick={() => onViewVariations(template)}
                variant="secondary"
                size="sm"
            >
                <Copy className="mr-2 h-4 w-4" />
                View Variations
            </Button>
        )}

        <div className="flex items-center space-x-2 pt-2 border-t w-full mt-2">
            <Switch 
              id={`pro-switch-${template.id}`} 
              checked={template.pro}
              onCheckedChange={handleTogglePro}
              disabled={isPending}
            />
            <Label htmlFor={`pro-switch-${template.id}`} className="text-sm font-medium">
              {template.pro ? "Pro Template" : "Standard Template"}
            </Label>
        </div>

      </CardFooter>
    </Card>
  );
}
